import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { LayoutService } from 'src/app/shared/services/app.layout.service';
import { UserService } from 'src/app/shared/services/user.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styles: [`
        :host ::ng-deep .pi-eye,
        :host ::ng-deep .pi-eye-slash {
            transform:scale(1.6);
            margin-right: 1rem;
            color: var(--primary-color) !important;
        }
    `]
})
export class RegisterComponent implements OnInit {
  registerForm!: FormGroup;
  registers: any[] = [];
  display: boolean = false;
  password!: string;

  constructor(private fb: FormBuilder, public layoutService: LayoutService, private userService: UserService) {

  }

  ngOnInit() {
    this.registerForm = this.fb.group({
      userName: ['', Validators.compose([
        Validators.required
      ])],
      password: ['', Validators.required],
      phoneNumber: ['', Validators.compose([
        Validators.required,
        Validators.minLength(10),
        Validators.maxLength(15),
        Validators.pattern(/^[0-9]*$/),
      ])],
      email: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)
      ])],
      roleId: [0],
    });
  }

  onSubmit() {
    const data = this.registerForm.value;
    data.phoneNumber = data.phoneNumber.toString();

    this.userService.register(data).subscribe((response: any) => {
      if (!!response.isSuccess) {
        window.alert((response.message || 'Are u Sure Leave From'));
      }
    });
  }
}
